import React, {Suspense} from "react";
import {Canvas} from "@react-three/fiber";
import {Sphere, MeshDistortMaterial} from "@react-three/drei";
import s from "./Home2.module.css"
import logo from "./home-logo.png";
import {FormCalc} from "../FormCalc/FormCalc";

const Hero = () => {

    return (
        <div className={s.home}>
            {/*<div className={s.overlay}/>*/}

            <div className={s.homeContent}>

                <div className={s.homeLeft}>
                    <img src={logo} className={s.homeLogo} alt=""/>
                    <h1>
                           
                    </h1>
                    <h2 style={{fontWeight: 'normal'}}>
                         25  240 /,    1 
                    </h2>
                    <hr/>
                    <h3 style={{fontWeight: 'normal'}}>     :</h3>
                    <ul>
                        <li data-emoji="">    </li>
                        <li data-emoji="">2-3     </li>
                        <li data-emoji=""> </li>
                        <li data-emoji="">   12 </li>
                    </ul>
                    <FormCalc/>
                </div>

                <div className={s.homeRight}>
                    <Canvas className={s.canvas}>
                        <Suspense fallback={null}>

                            <ambientLight intensity={1.3}/>
                            <directionalLight position={[3, 2, 1]}/>
                            {
                                // @ts-ignore
                                <Sphere args={[1, 100, 200]} scale={1.6}>
                                    <MeshDistortMaterial
                                        color="rgb(81, 115, 36)"
                                        attach="material"
                                        distort={0.5}
                                        speed={0.5}
                                    />
                                </Sphere>
                            }
                        </Suspense>
                    </Canvas>
                    {/*<div className={s.canvas}></div>*/}
                </div>

            </div>

        </div>
    );
};

export default Hero;